*,
*::before,
*::after{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

.flexRow {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: row;
  max-height: 100vh;
  justify-content: center;
}

.flexColumn {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 100vh;
  justify-content: space-around;
}

.grid {
  display: grid;
  grid-template-areas:
    "headerLeft header headerRight"
    "nav article ads"
    "footerLeft footer footerRight";
  grid-template-rows: 14% 70% 14%;
  grid-template-columns: 15% 70% 15%;
  grid-row-gap: 2%;
  height: 100vh;
  margin: 0;
  align-items: center;
  justify-items: center;
  }

@media all and (max-width: 575px) {
  .grid {
    grid-template-rows: 14% 63% 14%;
    grid-template-columns: 10% 80% 10%;
    grid-row-gap: 0;
  }
}

#header {
  grid-area: header;
  }

#headerRight {
  grid-area: headerRight;
}

#pageFooter {
  grid-area: footer;
  }
#mainArticle {
  width: 100%;
  height: 100%;
  grid-area: article;
  }
#mainNav {
  grid-area: nav;
  }
#siteAds {
  grid-area: ads;
  }
